
img {
  width: 100%;
}
.card {
  background-color: #4e4376;
  margin: 0;
}
.card .info {
    padding: .3rem .8rem;
    height: 3rem;
}
.card .info .desc {
      color: #fff;
      height: 1rem;
      overflow: hidden;
}
.card .info .price {
      height: 2rem;
}
.card .info .price p {
        display: inline-block;
        font-size: 16px;
}
.card .info .price span {
        display: inline-block;
        vertical-align: middle;
}
.card .info .price .price-count {
        color: #FFC107;
        font-size: .8rem;
        margin-right: 1rem;
}
.card .info .price .price-origin {
        position: relative;
        margin: 0;
        font-size: .6rem;
        color: #9e9e9e;
        padding-top: .1rem;
}
.card .info .price .price-origin:before {
          content: '';
          display: block;
          position: absolute;
          top: 50%;
          left: -5%;
          width: 115%;
          height: 2px;
          background: #9e9e9e;
}



