
@charset "UTF-8";
body, div, span, header, footer, nav, section, aside, article, ul, dl, dt, dd, li, a, p, h1, h2, h3, h4, h5, h6, i, b, textarea, button, input, select, figure, figcaption {
  padding: 0;
  margin: 0;
  list-style: none;
  font-style: normal;
  text-decoration: none;
  border: none;
  color: #333;
  box-sizing: border-box;
  font-family: Arial,Verdana,Sans-serif;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
}
body:focus, div:focus, span:focus, header:focus, footer:focus, nav:focus, section:focus, aside:focus, article:focus, ul:focus, dl:focus, dt:focus, dd:focus, li:focus, a:focus, p:focus, h1:focus, h2:focus, h3:focus, h4:focus, h5:focus, h6:focus, i:focus, b:focus, textarea:focus, button:focus, input:focus, select:focus, figure:focus, figcaption:focus {
    outline: none;
}
body, html {
  overflow-x: initial !important;
  overflow-y: initial !important;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  background-color: #F5F5F5;
}

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 1px transparent;
  border-radius: 10px;
  background-color: #F5F5F5;
}

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}
input[type="button"], input[type="submit"], input[type="search"], input[type="reset"] {
  -webkit-appearance: none;
}
textarea {
  -webkit-appearance: none;
}
html, body {
  width: 100%;
  background-color: #F5F5F5;
}
img {
  display: block;
}
.clearfix:after {
  content: '';
  display: block;
  clear: both;
}
.clearfix {
  zoom: 1;
}
.back_img {
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.margin {
  margin: 0 auto;
}
.left {
  float: left;
}
.right {
  float: right;
}
.hide {
  display: none;
}
.show {
  display: block;
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.paddingTop {
  padding-top: 1.95rem;
}
.paddingBottom {
  padding-bottom: 2.25rem;
}
.child-page {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  background-color: #f8f8f8;
  z-index: 1070;
}
.head-title {
  position: absolute;
  color: #fff;
  line-height: 1.95rem;
  left: 14%;
  font-size: .7rem;
}
.head-save {
  position: absolute;
  color: #fff;
  line-height: 1.95rem;
  right: 3%;
  font-size: .7rem;
}
.router-fade-enter-active, .router-fade-leave-active {
  transition: opacity .2s;
}
.router-fade-enter, .router-fade-leave-active {
  opacity: 0;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.router-slid-enter-active, .router-slid-leave-active {
  transition: all .4s;
}
.router-slid-enter, .router-slid-leave-active {
  transform: translate3d(2rem, 0, 0);
  opacity: 0;
}
.router-float-enter-active, .router-float-leave-active {
  transition: all .2s;
}
.router-float-enter, .router-float-leave-active {
  transform: translate3d(0, 30%, 0);
  opacity: 0;
}
.shadow {
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.156863), 0 3px 10px rgba(0, 0, 0, 0.227451);
}
.shadow-white {
  box-shadow: 0 3px 10px rgba(255, 255, 255, 0.156863), 0 3px 10px rgba(255, 255, 255, 0.227451);
}
.app {
  height: 100%;
}
.head_logo {
  left: .2rem;
  width: 1.6rem;
  height: 1rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("images/logo.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.wrap {
  height: 100%;
  overflow: auto;
  background: #eee;
}



